import React, { Component } from 'react';
import moment from 'moment';
import Widget from './Widget';
import WidgetBase from './WidgetBase';
import WidgetHeader from './WidgetHeader';
import { getNextEvent } from './helpers';

class WidgetRiders extends WidgetBase {
  render() {
    const momentNow = this.props.momentNow;
    const nextItem = getNextEvent(this.props.data, momentNow);
    const dateStart = moment.parseZone(nextItem.dateStart);
    const dateEnd = moment.parseZone(nextItem.dateEnd);
    const isGameInProgress = momentNow.isAfter(dateStart) && momentNow.isBefore(dateEnd);
    const value1 = isGameInProgress ? nextItem.state : dateStart.format('llll');
    const value2 = isGameInProgress ?
      `${nextItem.awayTeam} ${nextItem.awayScore} vs ${nextItem.homeTeam} ${nextItem.homeScore}` :
      `${nextItem.awayTeam} vs ${nextItem.homeTeam}`;
    const shouldWarn = isGameInProgress;
    const icon = './icons/riders.svg';
    const rows = this.props.data.map((x) => {
      return <tr>
        <td className="text-right no-wrap">{moment.parseZone(x.dateStart).format('lll')}</td>
        <td className="text-center">{x.state}</td>
        <td>{x.awayTeam}</td>
        <td className="text-right">{x.awayScore}</td>
        <td>{x.homeTeam}</td>
        <td className="text-right">{x.homeScore}</td>
      </tr>;
    });
    return (
      <Widget expanded={this.state.expanded}>
        <WidgetHeader title="Riders" value1={value1} value2={value2} icon={icon} toggleExpanded={this.toggleExpanded} shouldWarn={shouldWarn} />
        <div className="row-body">
          <table className="table table--collapse table--striped">
            <thead>
            <tr>
              <th>Date</th>
              <th>State</th>
              <th colspan="2">Away</th>
              <th colspan="2">Home</th>
            </tr>
            </thead>
            <tbody>
            {rows}
            </tbody>
          </table>
        </div>
        <div className="row-footer">
          &nbsp;
        </div>
      </Widget>
    );
  }
}

export default WidgetRiders;



