import React, { Component } from 'react';
import moment from 'moment';
import Widget from './Widget';
import WidgetBase from './WidgetBase';
import WidgetHeader from './WidgetHeader';
import titleCase from 'title-case';

class WidgetCriminalOffences extends WidgetBase {
  render() {
    const nextItem = this.props.data.length ? this.props.data[0] : {};
    const value1 = moment.parseZone(nextItem.date).format('lll');
    const value2 = nextItem.category;
    const icon = './icons/other/svg/014-people.svg';
    const rows = this.props.data.map((x) => {
      return <tr>
        <td className="text-right no-wrap">{moment.parseZone(x.date).format('ll')}</td>
        <td>{titleCase(x.location)}</td>
        <td>{x.category}</td>
      </tr>;
    });
    return (
      <Widget expanded={this.state.expanded}>
        <WidgetHeader title="Criminal Offences" value1={value1} value2={value2} icon={icon} toggleExpanded={this.toggleExpanded} />
        <div className="row-body">
          <table className="table table--collapse table--striped table--small">
            <thead>
            <tr>
              <th>Date</th>
              <th>Location</th>
              <th>Category</th>
            </tr>
            </thead>
            <tbody>
            {rows}
            </tbody>
          </table>
        </div>
        <div className="row-footer">
          <b>Location:</b> {titleCase(nextItem.location)}
        </div>
      </Widget>
    );
  }
}

export default WidgetCriminalOffences;



